/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.qodef-woo-single-page {
	
	&.qodef-woo-single-thumb-on-left-side {

		.qodef-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 130px;
							width: calc(100% - 130px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 115px;
								height: calc(25% + 4px);
								padding: 0 0 16px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (4 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include qodefRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .qodef-single-product-content {
        @include qodefRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 51%;
		    margin: 0;
		    padding: 0 33px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include qodefRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .qodef-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 49%;
        padding: 0;
        float: left;
        box-sizing: border-box;
	
	    .qodef-single-product-title {
		    display: block;
		    margin: 0;
			font-weight: 600;
			letter-spacing: 0.05em;
	    }
	
	    .price {
		    @include qodefWooCommercePriceStyle();

            margin: 20px 0 0;
	    }
	
	    .woocommerce-product-rating {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 3px 0 3px;

			.star-rating{
				color: #ffa800;
				letter-spacing:10px !important;
			}

			.woocommerce-review-link{
                text-transform:uppercase;
                font-size:12px;
                letter-spacing:0.15em;
                font-weight:400;
				color:$default-text-color;
			}
	    }

	
	    .woocommerce-product-details__short-description {
		    @include qodefRelativeHolderLayout();
			margin: 40px 0 0;
			line-height: 27px;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 79px 0 0;
			padding:17px 0 11px;
            border-top: 1px solid $default-border-color;
            border-bottom: 1px solid $default-border-color;
		
		    > span {
				display: block;
				margin: 4px 0 8px;
				color:$default-heading-color;
				font-size: 15px;
				text-transform: uppercase;
				font-weight: 700;
				font-family: $default-heading-font;

			    a,.sku {

                    &:first-child {
                        margin-left: 10px;
                    }

					color: $default-text-color;
					text-transform: uppercase;
					font-weight: 500;
					font-size: 13px;
					letter-spacing: .11em;
					font-family: $default-text-font;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .qodef-woo-social-share-holder {
		    @include qodefRelativeHolderLayout();
			margin: -2px 0 10px;
			color:$default-heading-color;
			font-size: 15px;
			text-transform: uppercase;
			font-weight: 700;
			font-family: $default-heading-font;
			border-bottom:1px solid $default-border-color;
			padding-bottom: 21px;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include qodefRelativeHolderLayout();
		    margin: 36px 0 0;
	    }
	
	    .qodef-quantity-buttons {
		    margin: 0 10px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include qodefRelativeHolderLayout();
		margin: 45px 0 0;
		
		ul.tabs {
			@include qodefRelativeHolderLayout();
			list-style: none;
			margin-bottom: 14px;
			padding: 0;
			border-bottom: 1px dashed $default-border-color;
			clear: both;

			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 15px 10px 0;
					font-weight: 600;
					font-size: 20px;
					font-family: $default-heading-font;
					text-transform: uppercase;
					letter-spacing: 0.05em;
					margin-right: 78px;

					&:hover{
						border-bottom:2px solid $first-main-color;
						margin-bottom:-2px;
					}

				}
			}
		}
		
		.entry-content {
			@include qodefRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 18px 0 20px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				font-size: 15px;
				text-transform: uppercase;
				font-weight: 700;
				font-family: $default-heading-font;
				padding: 4px 25px 0 0;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include qodefRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include qodefRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}

                .comment-form-rating {
                    margin: 20px 0;
                }
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include qodefWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}


	.related.products,
	.upsells.products {
		margin: 50px 0 0;
		
		> h5 {
			display: block;
			margin: 37px 0 57px;
		}
		
		.product {

			text-align: center;
			
			.qodef-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */