/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;

    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {

        ul {
            padding: 0;
            margin: 0;
            list-style: none;

            li {
                @include qodefRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;

                &:last-child {
                    margin: 0;
                }

                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }

    &.widget_shopping_cart {

        .widget_shopping_cart_content {
            @include qodefRelativeHolderLayout();

            ul {

                li {

                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }

                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;

                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }

            .variation {
                @include qodefRelativeHolderLayout();

                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;

                    p {
                        margin: 0;
                    }
                }
            }

            .total {
                margin: 17px 0 13px;
            }

            .buttons {
                margin: 10px 0 0;
            }
        }
    }

    &.widget_layered_nav {

        .qodef-widget-title {
            margin: 0 0 17px;
        }

        ul {

            li {
                margin: 2px 0;

                &.chosen {

                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }

    &.widget_layered_nav_filters {

        .qodef-widget-title {
            margin: 0 0 17px;
        }
    }

    &.widget_price_filter {

        .price_slider_wrapper {
            margin-top: 36px;

            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #e5e5e5;

                .ui-slider-handle {
                    position: absolute;
                    top: -5px;
                    height: 13px;
                    width: 13px;
                    border-radius: 100%;
                    background-color: $first-main-color;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;

                    &:last-child {
                        @include qodefTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: #29282d;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 31px 0 0;

            .button {
                float: right;
            }

            .price_label {
                float: left;
                font-size: 16px;
                color: $default-heading-color;
                font-family: $default-text-font;
                padding-top: 10px;
            }
        }
    }

    &.widget_product_categories {

        ul {
            margin-top: 20px;

            li {
                margin: 3px 0 4px 0;

                a {
                    font-size: 12px;
                }

                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }

    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {

        .qodef-widget-title {
            margin: 0 0 29px;
        }

        ul {

            li {
                min-height: 53px;

                a {

                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }

                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 68px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }

                > *:not(a) {
                    margin: 0 0 0 68px;
                    line-height: 20px;
                }

                .star-rating {
                    position: relative;
                    display: none;

                    span {
                        max-width: 100px;
                    }
                }

                del,
                ins {
                    display: block;

                    span {
                        color: inherit;
                    }
                }

                ins {
                    text-decoration: none;
                }
            }
        }
    }


    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {

        ul {
            margin-top: 20px;
            li {

                margin: 0 0 23px;

                .product-title {
                    color: inherit;
                    font-family: $default-heading-font;
                    font-size: 15px;
                    font-weight: 600;
                    letter-spacing: 0.05em;
                    margin-bottom: -2px;
                    margin-top: 18px;
                }

                a img {
                    width: 53px;
                    height: auto;
                }

                .amount {
                    color: $first-main-color;
                    font-family: $default-heading-font;
                    font-size: 15px;
                    font-weight: 600;
                    letter-spacing: 0;
                }

                del {
                    color: #ababab;
                    float: left;
                    margin-right: 10px;

                    .woocommerce-Price-amount {
                        color: #ababab;
                    }
                }
            }
        }
    }

    &.widget_recently_viewed_products {

        ul {

            li {

                del,
                > .amount {
                    display: block;
                }
            }
        }
    }

    &.widget_recent_reviews {

        ul {

            li {

                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }

        a {
            display: block;
            padding: 0 0 0 102px;
        }

        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }

    &.widget_top_rated_products {

        ul {

            li {

                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }

                .amount {
                    display: block;
                }
            }
        }
    }

    &.widget_product_tag_cloud {

        .tagcloud {

            padding-top: 30px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: $default-text-font;
                background-color: transparent;
                font-size: 12px !important;
                text-transform: uppercase;
                color: $second-text-color;
                background-color: #f2f2f2;
                padding: 2px 15px;
                margin: 0 2px 5px 0;
                letter-spacing: 0.25em;

                &:hover {
                    color:$first-main-color;
                }

                &:last-child {

                    &:after {
                        display: none;
                    }
                }
            }
        }
    }

    &.widget_product_search {

        .woocommerce-product-search {
            @include qodefRelativeHolderLayout();
            clear: both;

            > div {
                @include qodefRelativeHolderLayout();
                border: 1px solid $default-border-color;
                box-sizing: border-box;
            }

            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 54px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include qodefTransition(color .2s ease-out);
            }

            input {
                width: calc(100% - 40px);
                padding: 10px 10px 10px 20px;
                font-family: inherit;
                font-size: 13px;

                @include qodefPlaceholder {
                    color: inherit;
                }
            }

            button {
                width: 40px;
                padding: 10px 13px 10px 4px;
                font-size: 14px;
                cursor: pointer;

                &:hover {
                    color: $default-heading-color;
                }

                span {
                    display: block;
                    line-height: inherit;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */