/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.qodef-pl-holder {
    @include qodefRelativeHolderLayout();


    .qodef-pli-inner-holder {
        display: inline-block;
        vertical-align: middle;
    }

    .qodef-pli {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        box-sizing: border-box;

	    .qodef-pli-title {
		    margin: 0;
	    }
	
	    .qodef-pli-category {
		    margin: 11px 0 3px;
	    }

        .qodef-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .qodef-pli-rating-holder {
		    @include qodefRelativeHolderLayout();
		    line-height: 1;
		    margin: 6px 0 6px;
	    }

	    .qodef-pli-rating {
		    @include qodefWooCommerceRatingsStyle();
	    }
        .qodef-pli-price {
	        @include qodefWooCommercePriceStyle();
            margin: 15px 0 0;
        }

        .qodef-pli-text-wrapper {
            @include qodefRelativeHolderLayout();
            padding: 25px 0 28px;
            text-align: center;
            @include qodefTransition(margin .35s cubic-bezier(0.63, 0.13, 0.25, 1),padding .35s cubic-bezier(0.63, 0.13, 0.25, 1));
        }

        &:hover {

            .qodef-pli-text {
                opacity: 1;
            }
        }
    }

    .qodef-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touch & {
            cursor: pointer;
        }

        .qodef-pli-image {
            @include qodefRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .qodef-pli-onsale,
            .qodef-pli-out-of-stock,
            .qodef-pli-new-product {
	            @include qodefWooCommerceMarksGeneralStyle();
            }

            .qodef-pli-onsale {
	            @include qodefWooCommerceSaleStyle();
            }

            .qodef-pli-out-of-stock {
	            @include qodefWooCommerceOutOfStockStyle();
            }
	
	        .qodef-pli-new-product {
		        @include qodefWooCommerceNewProductStyle();
	        }
        }
            
        .qodef-pli-text {
            @include qodefAbsoluteHolderLayout();
            padding: 10px;
            opacity: 0;
            box-sizing: border-box;
            @include qodefTransition(opacity .2s ease-out);
        }

    }

    &.qodef-product-add-to-cart-below-image {

        .qodef-pli {

            .qodef-pli-image img {
                @include qodefTransitionTransform(0.35s cubic-bezier(0.63, 0.13, 0.25, 1));
            }

            .qodef-pli-inner-holder {
                box-shadow: 0px 0px 13px 3px rgba(0,0,0,0.05);
            }

            .qodef-pli-text-wrapper {
                background-color: #fff;

                .button {
                    display: block;
                    width: 100%;
                    margin: 0;
                    font-size: 12px;
                    text-transform: uppercase;
                    color: $default-text-color;
                    font-weight: 600;
                    letter-spacing: 0.1em;
                    font-family: $default-text-font;
                    position: absolute;
                    left: 50%;
                    @include qodefTransform(translateX(-50%));
                    bottom: 22px;
                    opacity: 0;
                    visibility: hidden;
                    @include qodefTransition(opacity .15s cubic-bezier(.86,.15,.19,.93),visibility .15s cubic-bezier(.86,.15,.19,.93));

                    &.added {
                        display:none;
                    }

                    &:after {
                        content: '+';
                        margin-left: 10px;
                        font-size: 17px;
                        font-weight: 400;
                    }
                }
            }

            &:hover {

                .qodef-pli-text-wrapper {
                    margin-top: -39px;
                    padding-bottom: 67px;
                    .button {
                        opacity: 1;
                        visibility: visible;
                        @include qodefTransition(opacity .35s cubic-bezier(0.63, 0.13, 0.25, 1) .1s,visibility .35s cubic-bezier(0.63, 0.13, 0.25, 1) .1s);
                    }
                }

                .added_to_cart.wc-forward { 
                    visibility:visible;
                    opacity:1;
                    @include qodefTransition(opacity .35s cubic-bezier(0.63, 0.13, 0.25, 1) .1s,visibility .35s cubic-bezier(0.63, 0.13, 0.25, 1) .1s);
                }

                .qodef-pli-image img {
                    @include qodefTransform(translate3d(0,-5%,0))
                }
            }

            .added_to_cart.wc-forward {
                font-size: 12px;
                text-transform: uppercase;
                color: $default-text-color;
                font-weight: 600;
                letter-spacing: .1em;
                font-family: $default-text-font;
                visibility:hidden;
                opacity:0;
                position: absolute;
                left: 50%;
                @include qodefTransform(translateX(-50%));
                bottom: 22px;
            }
        }
    }

    &.qodef-product-add-to-cart-over-image {

        .qodef-pli {

            .qodef-pli-image img {
                @include qodefTransition(opacity 0.2s ease-out);
            }

            &:hover {
                .qodef-pli-image img {
                    opacity: 0.3;
                }
            }
        }

        .qodef-pli-add-to-cart {
            position: absolute;
            top: 50%;
            left: 50%;
            @include qodefTransform(translate(-50%, -50%));
            text-align: center;
            width: 100%;
            .button {
                @include qodefButtonDefaultStyle();
                @include qodefButtonSmallParams();
                @include qodefButtonSolidColor();
                cursor: pointer;

                &.added {
                    display:none;
                }
            }

            .added_to_cart {
                padding: 14px 25px 16px;
                font-size: 12px;
                line-height: 14px;
                color: $default-background-color;
                background-color: $first-main-color;
                text-transform: uppercase;
                letter-spacing: 2px;
                font-weight: 700;
            }

        }
    }
	
	&.qodef-product-info-light {
		
		.qodef-pli-inner-holder {
			
			.qodef-pli-text-wrapper {
				
				.qodef-pli-title,
                .qodef-pli-price {
					color: #fff;
				}
			}
		}
	}
	
	&.qodef-product-info-dark {
		
		.qodef-pli-inner-holder {
			
			.qodef-pli-text-wrapper {
				
				.qodef-pli-title,
                .qodef-pli-price {
					color: $default-heading-color;
				}
			}
		}
	}
	
	/*************** Standard Layout - begin ***************/
	
	&.qodef-standard-layout {
		
		.qodef-pli {
			float: left;
		}
		
		&.qodef-large-space {
			margin: 0 0 -50px;
		}
		
		&.qodef-normal-space {
			margin: 0 0 -30px;
		}
		
		&.qodef-small-space {
			margin: 0 0 -20px;
		}
		
		&.qodef-tiny-space {
			margin: 0 0 -10px;
		}
		
		$standard_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($standard_list_columns) {
			&.qodef-#{nth($standard_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.qodef-pli {
					width: $column_width;
					
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						&:nth-child(#{$i+2}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
	
	/*************** Standard Layout - end ***************/
	
	/*************** Masonry Layout - begin ***************/
	
	&.qodef-masonry-layout {
		
		.qodef-pl-outer {
			opacity: 0;
		}
		
		.qodef-pl-sizer {
			width: 100%;
		}
		
		.qodef-pl-gutter {
			width: 0;
		}
		
		.qodef-pli-inner {
			width: 100%;
			height: 100%;
			
			.qodef-pli-image {
				height: 100%;
				
				img {
					height: 100%;
				}
			}
		}
		
		.qodef-pli.qodef-woo-image-large-width {
			
			img {
				width: 100%;
			}
		}
		
		$masonry_list_columns: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($masonry_list_columns) {
			&.qodef-#{nth($masonry_list_columns,$i+1)}-columns {
				$column_width: 100%/($i+2);
				
				.qodef-pli,
				.qodef-pl-sizer {
					width: $column_width;
				}
				
				.qodef-pli.qodef-woo-image-large-width,
				.qodef-pli.qodef-woo-image-large-width-height {
					width: $column_width*2;
				}
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */