/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {

    input[type="submit"],
    button[type="submit"]:not(.qodef-woo-search-widget-button),
    .wc-forward:not(.added_to_cart):not(.checkout-button),
    a.checkout-button,
    a.woocommerce-Button {
        @include qodefButtonDefaultStyle();
        @include qodefButtonMediumParams();
        @include qodefButtonSolidColor();
        background-color: $first-main-color;
        cursor: pointer;
        z-index: 3;
        margin-top: -1px;
        padding: 9px 24px 9px 24px;
        font-family: $default-text-font;
        font-size: 14px;
        letter-spacing: 0.12em;
        line-height: 28px;

        &:hover {
            background-color: $default-heading-color;
        }
    }

    a.button.added {
        display: none;
    }
}

.woocommerce-page .qodef-content .wc-forward:not(.added_to_cart):not(.checkout-button) {
    padding: 9px 0px;
    margin: 0 0 50px;
    font-family: Ubuntu, sans-serif;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.2em;
}

/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */