/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.qodef-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;
    color: $default-text-color;

    .touch & {
        cursor: pointer;
    }

    .qodef-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .qodef-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .qodef-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: middle;
        }

        .qodef-cart-number {
            font-size:12px;
            background-color:$first-main-color;
            color:$default-background-color;
            height: 14px;
            width: 14px !important;
            display: inline-block;
            line-height: 14px;
            text-align: center;
            border-radius: 100%;
            position: relative;
            bottom: 10px;
            right: 9px;
        }

        &.qodef-header-cart-icon-pack {
            .qodef-cart-icon {
                font-size: 20px;
                line-height: 1em;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        &.qodef-header-cart-svg-path {
            .qodef-cart-icon {

                >* {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .qodef-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.qodef-dark-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $first-main-color;
    }
}

.qodef-light-header .qodef-page-header > div:not(.qodef-sticky-header):not(.fixed) .qodef-shopping-cart-holder .qodef-header-cart {
    color: $header-light-color;

    &:hover {
        color: $first-main-color;
    }
}

.qodef-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 280px;
    background-color: #1c1c1c;
    border: 1px solid #1c1c1c;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include qodefTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 25px;
        padding: 0;

        li {
            @include qodefTableLayout();
            padding: 12px 0;
            box-sizing: border-box;

            &.qodef-empty-cart {
                border: 0 !important;
                color: $default-text-color
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
                border-top:1px solid #292929;
            }
        }

    }

    .qodef-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 52px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .qodef-item-info-holder {
        @include qodefTableCellLayout();
        vertical-align: middle;
        padding-left: 13px;

        .qodef-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            color:$header-light-color;
            font-family: $default-heading-font;
            font-size:14px;
            margin-top: 5px;
            margin-bottom: -5px;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;
                color:$default-text-color;
                margin-top: 8px;
                font-size: 17px;

                &:hover{
                    color:$default-text-color;
                }

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .qodef-quantity {
            display: inline;
            margin: 3px 0 0;
            font-size: 14px;
        }

        .amount {
            display: inline-block;
            margin: 3px 0 0;
            color: $default-text-color;
            font-size:14px;
        }
    }

    .qodef-cart-bottom {
        padding: 12px 0 0;

        .qodef-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 14px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $header-light-color;
                line-height: 20px;
            }

            .qodef-total{
                font-size: 15px;
                font-family: $default-heading-font;
                font-weight: 600;
            }

            .qodef-total-amount {
                text-align: right;
	            font-size: 14px;

            }
        }

        .qodef-view-cart {
            @include qodefRelativeHolderLayout();
            height: 32px;
            line-height: 32px;
            padding: 0 10px;
            font-size: 10px;
            font-weight: inherit;
	        letter-spacing: 2px;
            color: #fff;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include qodefTransition(color .2s ease-out, background-color .2s ease-out);
            width: calc(50% - 3px);
            float: left;
            text-align: center;

            span {
                display: block;
            }

            &:hover {
                color: $default-heading-color;
                background-color: $default-background-color;
            }
        }

        .qodef-view-checkout{
            @include qodefRelativeHolderLayout();
            height: 32px;
            line-height: 32px;
            padding: 0 10px;
            font-size: 10px;
            font-weight: inherit;
            letter-spacing: 2px;
            color: #fff;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include qodefTransition(color .2s ease-out, background-color .2s ease-out);
            width: calc(50% - 3px);
            margin-left: 6px;
            float: left;
            text-align: center;


            span {
                display: block;
            }

            &:hover {
                color: $default-heading-color;
                background-color: $default-background-color;
            }
        }
    }
}

.qodef-vertical-menu-area {

    .qodef-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */