/* ==========================================================================
   WooCommerce Checkout page styles - begin
   ========================================================================== */

.qodef-woocommerce-page {

    .checkout_coupon {
        display: block;
        margin: 0 0 34px;

        p {
            margin: 0;
        }
    }

    &.woocommerce-checkout form.login {
        display: block;
        margin: 0 0 34px;

        > *:first-child {
            margin: 0 0 15px;
        }

        p {
            margin: 0;
        }

        input[type='checkbox'] {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            top: -1px;
            margin: 0 3px 0 5px;
        }

        .lost_password {
            display: block;
            margin: 15px 0 0;
        }
    }

    .woocommerce-checkout .col2-set:before,
    .woocommerce-checkout .col2-set:after {
        content: '';
        display: table;
    }

    .woocommerce-checkout .col2-set:after {
        clear: both;
    }

    .woocommerce-checkout {

        .col-1,
        .col-2 {
            position: relative;
            display: inline-block;
            width: 49%;
            margin-right: 1%;
            float: left;
            vertical-align: middle;

            h3 {
                margin: 8px 0 25px;
            }

            p {
                margin: 0;
            }

            label:not(.checkbox) {
                display: block;
                margin: 0 0 5px;
            }

            .form-row {
                width: 49%;
                margin-right: 2%;
                float: left;
            }

            .form-row-last {
                margin-right: 0;
            }

            .form-row-wide,
            .form-row.notes {
                width: 100%;
            }
        }

        .col-2 {
            margin-left: 1%;
            margin-right: 0;
        }

        .create-account {
            margin: 0 0 15px;

            input, label {
                display: inline-block;
                vertical-align: middle;
            }
        }

        h3#order_review_heading {
            margin: 28px 0 15px;
        }

        table {
            padding: 0;
            margin: 0 0 35px;

            tr {
                border: 0;
                border-bottom: 1px solid $default-border-color;

                &.shipping {

                    #shipping_method {
                        list-style: none;
                        margin: 0;

                        li {
                            margin: 3px 0;
                            padding: 0;

                            > * {
                                display: inline-block;
                                vertical-align: middle;
                                margin: 0;
                            }
                        }
                    }
                }
            }

            thead th {
                color: $default-heading-color;
            }

            td, th {
                padding: 14px 0;
                text-align: left;
                vertical-align: middle;
            }

            th {
                border: 0;

                &:first-child {
                    color: $default-heading-color;
                }
            }

            .variation {

                > * {
                    display: inline-block;
                    vertical-align: middle;
                    padding: 0;
                    margin: 0;
                }

                p {
                    margin: 0;
                }
            }
        }

        .select2-container {
            border-radius: 0;

            margin-bottom: 25px;

            .select2-selection.select2-selection--single {

                width: 100%;
                margin: 0;
                padding: 15px 20px;
                font-family: inherit;
                font-size: 15px;
                line-height: 20px;
                font-weight: inherit;
                background-color: $second-background-color;
                color: #313646;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                cursor: pointer;
                box-sizing: border-box;
                @include qodefTransition(border-color .2s ease-in-out);

                .select2-selection__rendered {
                    padding: 0;
                }

                .select2-selection__arrow {
                    padding-top: 15px;
                }
            }

        }

        input[type='radio'],
        input[type='checkbox'] {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            top: -1px;
            margin: 0 3px 0 5px;
        }

        .woocommerce-checkout-payment {
            display: block;
            margin: 20px 0 70px;

            ul {
                list-style: none;
                margin: 0 0 35px;
                padding: 0;

                li {
                    padding: 14px 0;
                    border-bottom: 1px solid $default-border-color;

                    input, label {
                        display: inline-block;
                        vertical-align: middle;
                    }

                    &.payment_method_paypal {

                        label {

                            img {
                                vertical-align: middle;
                                margin: 0 8px;
                            }
                        }
                    }
                }
            }
        }

            .woocommerce-checkout-review-order {

                .woocommerce-checkout-review-order-table {

                    tbody .product-total {
                        font-family: $default-heading-font;
                        font-size:20px;
                    }

                    tfoot {
                         .woocommerce-Price-amount {
                             font-family: $default-heading-font;
                             font-size:20px;
                         }
                    }

                }

            }
    }

}


body.woocommerce-checkout .select2-container--default.select2-container--open .select2-dropdown.select2-dropdown--below {
    border: 1px solid $default-border-color;
}

/* ==========================================================================
   WooCommerce Checkout page styles - end
   ========================================================================== */