/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .qodef-content,
div.woocommerce {

    .qodef-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;

        .qodef-quantity-input,
        .qodef-quantity-minus,
        .qodef-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            text-align: center;
            box-sizing: border-box;
        }

        .qodef-quantity-input {
            position: relative;
            height: 50px;
            width: 52px;
            padding: 0;
            margin: 0 25px 0 0;
            font-family: $default-text-font;
            line-height: 42px;
            outline: 0;
            font-size:15px;
            color:$default-heading-color;
            font-weight: 700;
        }

        .qodef-quantity-minus,
        .qodef-quantity-plus {
            position: absolute;
            right: 0;
            width: 24px;
            height: 25px;
            line-height: 28px;
            border-left: 0;
            cursor: pointer;
            @include qodefTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .qodef-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .qodef-quantity-minus {
            bottom: 0;
            border-top:0;
        }
    }
}

.woocommerce-page .qodef-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
        border-color:transparent;
    
        @include qodefPlaceholder {
            color: inherit;
        }
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */