/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.qodef-woo-pagination-holder {
    @include qodefRelativeHolderLayout();
    margin: 0 0 66px;
    clear: both;

    .qodef-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 99%;
    margin: 0;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0;

            a,
            span {
                position: relative;
                display: inline-block;
                margin: 0 6px;
                padding: 0;
                vertical-align: top;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                @include qodefTransition(color .2s ease-out, background-color .2s ease-out);
                height:44px;
                width:44px;
                text-align: center;
                line-height: 44px;
                background-color:#f2f2f2;
                color:$default-heading-color;
                font-size: 13px;
                font-weight: 700;
                font-family:$default-heading-font;

                &:hover,
                &.current {
                    background-color: $first-main-color;
                    color:$default-background-color;
                }
            }

            a {

                &.next,
                &.prev {
                    font-size: 0;

                    &:before {
                        position: relative;
                        top: -1px;
                        display: block;
                        font-family: 'Ionicons';
                        font-size: 18px;
                        line-height: inherit;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }
    
                &.prev {
        
                    &:before {
                        content: "\f3d2";
                    }
                }
    
                &.next {
        
                    &:before {
                        content: "\f3d3";
                    }
                }
            }
        }
    }
}

.qodef-woo-view-all-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 1%;
    bottom:2px;
    height: 44px;
    line-height: 44px;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0 0 0 2px;
        color: $default-text-color;
        font-size: 12px;
        font-family: $default-text-font;
        text-transform: uppercase;
        font-weight: 600;

        &:before {
            content: '|';
            padding: 0 14px 0 0;
            color: $default-text-color;
        }

        &:hover {
            color: $default-heading-color;
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */