/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.qodef-content .woocommerce {
    @include qodefRelativeHolderLayout();

    h2 {
        margin-bottom: 12px;
    }
}

.related.products h2 {
    font-size: 20px;
    margin-bottom: 58px;
}


.qodef-pl-main-holder {
    margin: 0 0 121px;
    
    .qodef-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;
        
        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .qodef-pl-inner .qodef-pl-text {
                opacity: 1;
            }
        }
        
        .qodef-product-list-title {
            margin: 0;
        }
        
        .qodef-pl-rating-holder {
            @include qodefRelativeHolderLayout();
            line-height: 1;
            margin: 6px 0 6px;
        }
        
        .price {
            @include qodefWooCommercePriceStyle();
        }

        .button {
            display: block;
            width: 100%;
            margin: 0;
            font-size: 12px;
            text-transform: uppercase;
            color:$default-text-color;
            font-weight:600;
            letter-spacing: 0.1em;
            font-family:$default-text-font;
            position: absolute;
            left: 50%;
            @include qodefTransform(translateX(-50%));
            bottom: 22px;
            opacity: 0;
            visibility: hidden;
            @include qodefTransition(initial);

            &:after{
                content: '+';
                margin-left: 10px;
                font-size: 17px;
                font-weight: 400;
            }

            &:not(.add_to_cart_button) {
                &:after {
                    position: relative;
                    top: 3px;
                    content: "\f10b";
                    font-family: "Ionicons";
                    font-size: 18px;
                    line-height: 18px;
                }
            }
        }

        .added_to_cart{
            display: block;
            width: 100%;
            margin: 0;
            font-size: 12px;
            text-transform: uppercase;
            color:$default-text-color;
            font-weight:600;
            letter-spacing: 0.1em;
            font-family:$default-text-font;
            position: absolute;
            left: 50%;
            @include qodefTransform(translateX(-50%));
            bottom: 22px;
            opacity: 0;
            visibility: hidden;
            @include qodefTransition(initial);
        }

        .button.added {
            display: none;
        }

        .qodef-pl-inner-holder {
            box-shadow: 0px 0px 13px 3px rgba(0,0,0,0.05);
            display: inline-block;
            vertical-align: middle;
        }
        
        .qodef-pl-inner {
            @include qodefRelativeHolderLayout();
            overflow: hidden;
            
            .qodef-pl-image {
                @include qodefRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                    @include qodefTransition(margin 0.2s ease-out);
                }
            }
            
            .qodef-pl-text {
                @include qodefAbsoluteHolderLayout();
                padding: 10px;
                opacity: 0;
                background-color: rgba($default-heading-color, 0.6);
                box-sizing: border-box;
                @include qodefTransition(opacity .2s ease-out);
            }
            
            .qodef-pl-text-outer {
                @include qodefTableLayout();
                text-align: center;
            }
            
            .qodef-pl-text-inner {
                @include qodefTableCellLayout();
                
                .qodef-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }
            }
            
            > a {
                @include qodefAbsoluteHolderLayout();
            }
        }
        
        .qodef-pl-text-wrapper {
            @include qodefRelativeHolderLayout();
            padding: 25px 0 24px;
            text-align: center;
            background-color: #fff;
            @include qodefTransition(margin 0.2s ease-out, padding 0.2s ease-out);
            @include qodefTransition(all .35s cubic-bezier(.63,.13,.25,1));

            .add_to_cart_button {
                @include qodefTransition(all .35s cubic-bezier(.63,.13,.25,1));

                &:hover {
                    color:$first-main-color;
                }
            }

            .ajax_add_to_cart {
                @include qodefTransition(color 0.5s ease-in-out);

                &:hover {
                    color:$first-main-color;
                }
            }
        }

        &:hover {
            .qodef-pl-text-wrapper {
                margin-top: -43px;
                padding-bottom: 67px;

                .added_to_cart,
                .button {
                    opacity: 1;
                    visibility: visible;
                    @include qodefTransition(opacity 0.1s ease-out 0.2s, visibility 0.1s ease-out 0.2s);
                }
            }

            .qodef-pl-image img {
                margin-top: -10px;
                margin-bottom: 10px;
            }
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.qodef-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.qodef-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .qodef-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */