/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 20px;
    left: 20px;
    width: auto;
    height: 25px;
    margin: 0;
    padding: 1px 13px 0 15px;
    color: $default-background-color;
    font-size: 15px;
    text-transform:uppercase;
    line-height: 26px;
    letter-spacing: 1px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include qodefTransform(translateZ(0));
    letter-spacing: 0.05em;
    font-family: $default-heading-font;
    font-weight: 500;
}


@mixin qodefWooCommerceSaleStyle() {
    background-color: $first-main-color;
    top: 20px;
    left: 20px;
}

@mixin qodefWooCommerceOutOfStockStyle() {
    background-color: $default-heading-color;
}

@mixin qodefWooCommerceNewProductStyle() {
    background-color: #4fc6cd;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin qodefWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 4px 0 0;
    line-height: 1;
    color:$first-main-color;
    font-weight: 600;
    font-family: $default-heading-font;
    font-size: 20px;

    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color:#ababab;
    }
    
    ins {
        text-decoration: none;
        color:$first-main-color;
    }

}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1.1em;
    letter-spacing: 2px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin qodefWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin qodefWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */